    .contact-container{
    width: 100%;
    height: 100vh;
    border: 1px solid white;

    }
    .contact-heading{
    margin-top: 90px;
    }
    .contact-main-section{
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    }
    .contact-left{

    margin-top: 7%;
    }
    .contact-right{
    width: 45%;
    border: 0px solid;
    }

    .form-input input{
    width: 80%;
    height: 50px;
    margin-top: 20px;
    border-radius: 10px;
    outline: none;
    border: 0px;
    padding-left: 20px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    }
    #messege-input>textarea{
    outline: none;
    border: 0px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    margin-top: 20px;
    padding-left: 20px;
    width: 80%;
    height: 200px;
    border-radius: 10px;
    word-wrap: normal;

    }
    .form-input button{
    width: 80%;
    height: 40px;
    border-radius: 10px;
    border: 0px;
    background-color:   #ffcd2d;
    color: rgb(255, 255, 255);
    font-weight: 600;

    }
    .form-input button:hover{
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    color: white;
    transition: 0.5s;
    }
    /* /..................... */
    @media screen and (max-width: 480px) {
    .heading{
    font-weight: 800;
    }
    .contact-main-section{
    margin: auto;
    border: 0px solid;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    }
    .contact-heading{
    margin-top: 60px;
    }

    .contact-left img{

    width: 80%;
    }
    .contact-right{
    width: 100%;
    border: 0px solid;
    }
    .contact-right button{
    margin-left: 20px;
    }

    .contact-heading h1{
    font-size: 25px;
    }
    .form-input{
    border: 0px solid;
    width: 93%;
    }
    .form-input input{
    width: 100%;
    }
    #messege-input>textarea{


    width: 100%;
    height: 200px;


    }
    }
    @media only screen and (min-width: 480px) and (max-width: 1024px){

    .contact-left img{
    width: 70%;
    }


    .contact-container{
    border: 0px solid;
    height: 100%;
    }
    }