*{
  font-family: 'Poppins', sans-serif;


    }
      .left_fixed{
        display: flex;
        justify-content: space-evenly;
        flex-direction: column;
        width: 3rem;
        height: 10rem;
        
        position: absolute;
        left: 0;
        z-index: 1000000;
        bottom: 0;
        position: fixed;
        text-align: center;
        align-items: center;margin: auto;
        font-size: 2rem;
      
       
      }
      .left_fixed a{
        color: orange;
      }
      @media screen and (max-width: 480px) {
        .left_fixed{
          display: none;
        }
      }
      body::-webkit-scrollbar{
        display: none;
      }   
   
      .resume-container{
        width: 100%;
        height: 100vh;
        border: 1px solid;
        background-color: rgb(232, 205, 205);
